@import '../../variables';

.InfoCardList {
    margin-top: 160px;
    margin-bottom: 160px;
    position: relative;
    .InfoCardList-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        .main-title {
            font-size: 32px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin-bottom: 60px;
        }
        .main-title-type1 {
            text-transform: uppercase;
        }
        .main-title-type2 {

        }
        .cards-list{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 80px;
        }
        .InfoCardList-background {
            width: 1280px;
            height: 75%;
            background-color: $primary-background-color;
            position: absolute;
            bottom: 33px;
            z-index: 0;
 
        }

        .InfoCardList-background-type1 {
            transform: translate(80px);
        }
        .InfoCardList-background-type2 {
            width: 115%;
        }


    }
}
