* {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-family: "Montserrat", sans-serif;
  line-height: 27px;
  box-sizing: border-box;
}

html {
  overflow-y: scroll; 
  overflow-x: hidden;
}
a {
  text-decoration: none;
}

h1 {
  font-size: 42px;
}

h2 {
  font-size: 32px;
}

h3 {
  font-size: 20px;
}

textarea,
input[type="text"],
input[type="button"],
input[type="submit"] {
     -webkit-appearance: none;
     border-radius: 0;
}

input:focus, textarea:focus, button:focus {
  outline: none;
}

*:focus {
  outline: none;
}

ul {
  list-style-type: none;
}

button {
  outline: none;
  border: none;
}

.container {
  width: 1180px;
  margin: 0 auto;
  position: relative;
}

@media only screen and (max-width: 1199px) {
  * {
    font-size: 16px;
  }
  .container {
    width: 100%;
    max-width: 900px;
    padding: 0 20px;
  }

  h1 {
    font-size: 24px;
  }
  
  h2 {
    font-size: 22px;
  }
  
  h3 {
    font-size: 20px;
  }
}

@media only screen and (max-width: 640px) {
  * {
    font-size: 14px;
  }

  section {
    margin: 70px 0;
  }

  h1 {
    font-size: 20px;
  }
  
  h2 {
    font-size: 18px;
  }
  
  h3 {
    font-size: 16px;
  }
}
